'use client'

import { useForm, useFieldArray } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Button } from '@/components/ui/button'
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { useState, useEffect } from 'react'
import { ImageUpload } from '@/components/ui/image-upload'
import { Checkbox } from '@/components/ui/checkbox'

const RENTAL_PLANS = ['Annual', 'Monthly', 'Weekly', 'One Day (24 Hours)'] as const
type RentalPlan = typeof RENTAL_PLANS[number]

export type FacilityType = 
  | 'individual-cabin'
  | 'coworking-spaces'
  | 'meeting-rooms'
  | 'bio-allied-labs'
  | 'manufacturing-labs'
  | 'prototyping-labs'
  | 'software'
  | 'saas-allied'
  | 'raw-space-office'
  | 'raw-space-lab'

// Base form fields
interface BaseFormFields {
  name: string
  description: string
  images: string[]
  videoLink?: string
  rentalPlans?: RentalPlan[]
  subscriptionPlans?: RentalPlan[]
  rentPerYear?: number
  rentPerMonth?: number
  rentPerWeek?: number
  dayPassRent?: number
}

// Equipment types
interface LabEquipment {
  labName: string
  equipmentName: string
  capacityAndMake: string
}

interface SoftwareEquipment {
  softwareName: string
  version: string
}

interface SaasEquipment {
  equipmentName: string
  capacityAndMake: string
}

// Facility-specific fields
interface IndividualCabinFields extends BaseFormFields {
  totalCabins: number
  availableCabins: number
}

interface CoworkingSpaceFields extends BaseFormFields {
  totalSeats: number
  availableSeats: number
}

interface MeetingRoomFields extends BaseFormFields {
  totalRooms: number
  seatingCapacity: number
  totalTrainingRoomSeaters: number
}

interface LabFields extends BaseFormFields {
  equipment: LabEquipment[]
}

interface SoftwareFields extends BaseFormFields {
  equipment: SoftwareEquipment[]
}

interface SaasFields extends BaseFormFields {
  equipment: SaasEquipment[]
}

interface AreaDetails {
  area: number;
  type: 'Covered' | 'Uncovered';
  furnishing: 'Furnished' | 'Not Furnished';
  customisation: 'Open to Customisation' | 'Cannot be Customised';
}

interface RawSpaceOfficeFields extends BaseFormFields {
  areaDetails: AreaDetails[];
}

interface RawSpaceLabFields extends BaseFormFields {
  areaDetails: AreaDetails[];
}

type FormData = 
  | IndividualCabinFields
  | CoworkingSpaceFields
  | MeetingRoomFields
  | LabFields
  | SoftwareFields
  | SaasFields
  | RawSpaceOfficeFields
  | RawSpaceLabFields

// Form validation schema
const createFormSchema = (type: FacilityType) => {
  const baseSchema = z.object({
    name: z.string().min(1, 'Name is required'),
    description: z.string().min(1, 'Description is required'),
    images: z.array(z.string()).min(1, 'At least one image is required'),
    videoLink: z.string().optional(),
    rentalPlans: z.array(z.enum(RENTAL_PLANS)).min(1, 'At least one rental plan is required'),
  })

  switch (type) {
    case 'individual-cabin':
      return baseSchema.extend({
        totalCabins: z.number().min(1, 'Must have at least 1 cabin'),
        availableCabins: z.number().min(0),
      })
    case 'coworking-spaces':
      return baseSchema.extend({
        totalSeats: z.number().min(1, 'Must have at least 1 seat'),
        availableSeats: z.number().min(0),
      })
    case 'meeting-rooms':
      return baseSchema.extend({
        totalRooms: z.number().min(1),
        seatingCapacity: z.number().min(1),
        totalTrainingRoomSeaters: z.number().min(0),
      })
    case 'bio-allied-labs':
    case 'manufacturing-labs':
    case 'prototyping-labs':
      return baseSchema.extend({
        equipment: z.array(z.object({
          labName: z.string().min(1),
          equipmentName: z.string().min(1),
          capacityAndMake: z.string().min(1),
        })).min(1),
      })
    case 'software':
      return baseSchema.extend({
        equipment: z.array(z.object({
          softwareName: z.string().min(1),
          version: z.string().min(1),
        })).min(1),
      })
    case 'saas-allied':
      return baseSchema.extend({
        equipment: z.array(z.object({
          equipmentName: z.string().min(1),
          capacityAndMake: z.string().min(1),
        })).min(1),
      })
    case 'raw-space-office':
      return baseSchema.extend({
        areaDetails: z.array(z.object({
          area: z.number().min(1, 'Area must be greater than 0'),
          type: z.enum(['Covered', 'Uncovered']),
          furnishing: z.enum(['Furnished', 'Not Furnished']),
          customisation: z.enum(['Open to Customisation', 'Cannot be Customised'])
        })).min(1, 'At least one area detail is required'),
      })
    case 'raw-space-lab':
      return baseSchema.extend({
        areaDetails: z.array(z.object({
          area: z.number().min(1, 'Area must be greater than 0'),
          type: z.enum(['Covered', 'Uncovered']),
          furnishing: z.enum(['Furnished', 'Not Furnished']),
          customisation: z.enum(['Open to Customisation', 'Cannot be Customised'])
        })).min(1, 'At least one area detail is required'),
      })
    default:
      return baseSchema
  }
}

interface FacilityFormProps {
  type: FacilityType
  onSubmit: (data: FormData) => void
  onChange?: () => void
}

